/* elements */

body {
  margin: 0;
  position: fixed;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  font-family: 'Questrial', 'Roboto', 'Raleway', 'Monteserrat';
  font-size: 1.1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

footer {
  color: #7f7f7f;
}

html {
  position: fixed;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
}

/* classes */

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-content {
  overflow-y: auto;
  overflow-x: hidden;
}

.certificate {
  height: 200px;
}

.global-footer {
  font-size: 15px;
  padding-left: 20px;
  bottom: 0;
  flex: none;
}

.img-thumbnail {
  height: 100%;
  overflow: hidden;
  margin-left: 15px;
  margin-right: 15px;
}

.img-thumbnail:hover > div > .thumbnail-image {
  transform: scale(1.05);
}

.jumbotron {
  opacity: 0.9;
  font-size: 1.3rem;
}

.jumbotron h1 {
  font-size: 4rem;
}

.page-content{
  padding: 1.5% 3% 1%;
}

.nav-item {
  color: #ffffff;
}

.navbar {
  flex: none;
}
.resume-subsection {
  margin-left: 1%;
  margin-right: 1%;
}

.thumbnail {
  margin-top: 15px;
  margin-bottom: 20px;
  padding-left: 15px;
  padding-right: 15px;
}

.thumbnail-container {
  display: flex;
  flex-wrap: wrap;
}

.thumbnail-image {
  width: 100%;
}

.thumbnail-image-container {
  overflow: hidden;
}
/* ids */

#about-background {
  background-image: url("./pictures/background.gif");
  background-position: 25% 30%;
  background-repeat: no-repeat;
  background-size: cover;
  flex: 1 1 auto;
  padding-top: 7%;
  padding-right: 15%;
}

